








import Vue, { PropType } from 'vue'
import { PlantTag } from '@/types/plant'

export default Vue.extend({
  name: 'VTag',
  props: {
    selected: {
      type: Boolean,
      default: false,
    },
    tag: {
      type: Object as PropType<PlantTag>,
    },
  },
  methods: {
    toggleSelection() {
      this.$emit('tag-selected', { selected: !this.selected, tag: this.tag })
    },
  },
})
