






import Vue from 'vue'

const colors = ['green', 'blue', 'yellow', 'red', 'grey', 'white']

export default Vue.extend({
  name: 'VButton',

  props: {
    color: {
      type: String,
      default: 'green',
      validator: v => colors.includes(v),
    },
    small: {
      type: Boolean,
      default: false,
    },
    round: {
      type: Boolean,
      default: false,
    },
    border: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    btnClass(): string[] {
      return [
        'btn',
        this.color,
        this.small && 'small',
        this.border && 'border',
        this.round && 'round',
      ]
    },
  },
})
