



















































import Vue from 'vue'
export default Vue.extend({
  name: 'AppLogo',
  props: {
    color: {
      type: String,
      validator: v => ['white', 'green'].includes(v),
    },
  },
  computed: {
    logoColor() {
      if (this.color === 'green') {
        return 'var(--brand-green-dark)'
      }
      return 'var(--brand-white)'
    },
  },
})
