











import Vue, { VueConstructor } from 'vue'
import { mapState } from 'vuex'
import { NotificationsState, NotificationsType } from '../store/state'

interface NotificationMapState {
  message: string
  type: NotificationsType
}

export default (Vue as VueConstructor<Vue & NotificationMapState>).extend({
  name: 'AppNotification',

  // components: {
  //   'feather-check': () =>
  //     import('vue-feather-icons/icons/CheckCircleIcon' /* webpackChunkName: "icons" */),
  //   'feather-alert': () =>
  //     import('vue-feather-icons/icons/AlertCircleIcon' /* webpackChunkName: "icons" */),
  // },

  computed: {
    ...mapState<NotificationsState>('notifications', {
      message: (state: NotificationsState) => state.message,
      type: (state: NotificationsState) => state.type,
    }),
    icon(): string {
      let icon = 'feather-check'
      switch (this.type) {
        case 'info':
        case 'alert':
          icon = 'feather-alert'
          break
        case 'success':
        default:
          icon = 'feather-check'
      }
      return icon
    },
  },
})
