











import Vue from 'vue'
let id = 0
export default Vue.extend({
  name: 'LabelGroup',
  props: {
    error: [Boolean, String],
    id: String,
    label: String,
    color: {
      type: String,
      default: 'white',
      validator: v => ['white', 'green'].includes(v),
    },
  },
  components: {
    'feather-alert': () =>
      import('vue-feather-icons/icons/AlertCircleIcon' /* webpackChunkName: "icons" */),
  },
  computed: {
    ariaDescribedBy() {
      return `labelgroup-${id}`
    },
    titleColor(): string {
      if (this.color === 'white') {
        return 'special'
      }
      return 'normal'
    },
  },
  created() {
    id++
  },
})
