




















import Vue from 'vue'

export default Vue.extend({
  name: 'AppHeader',

  components: {
    'feather-left': () =>
      import('vue-feather-icons/icons/ArrowLeftIcon' /* webpackChunkName: "icons" */),
  },

  props: {
    returnTo: {
      type: [String, Boolean],
      default: false,
    },
    color: {
      type: String,
      default: 'green',
      validator: (v) => ['white', 'green'].includes(v),
    },
  },

  computed: {
    titleColor(): string {
      if (this.color === 'white') {
        return 'special'
      }
      return 'normal'
    },
  },
})
