




import Vue from 'vue'
import { HTMLElementEvent } from '@/types/events'

export default Vue.extend({
  name: 'VTextarea',
  props: {
    value: String,
    error: Boolean,
    errorMessage: String,
  },
  methods: {
    handleInput(event: HTMLElementEvent<HTMLInputElement>) {
      this.$emit('input', event.target.value)
    },
  },
})
