








import Vue from 'vue'
import { HTMLElementEvent } from '@/types/events'

export default Vue.extend({
  name: 'VInput',
  props: {
    value: String,
    error: Boolean,
    errorMessage: String,
  },
  methods: {
    handleInput(event: HTMLElementEvent<HTMLInputElement>) {
      this.$emit('input', event.target.value)
    },
    focus() {
      ;(this.$refs.input as HTMLInputElement).focus()
    },
  },
})
